@import "../../../../variables.scss";

.Bus {
  box-sizing: border-box;
  @include display_flex_row;
  justify-content: space-apart;
  width: 100%;
  height: 20%;
  font-size: 10px;
  @media screen and (min-width: 550px) {
    font-size: 12px;
  }
  border-left: 2px solid $busgrey;
  border-right: 2px solid $busgrey;

  .BusService {
    width: 20%;
    text-align: left;
  }
  .BusDestination {
    width: 40%;
    text-align: left;
  }
  .BusDepartureTime {
    width: 20%;
    text-align: left;
  }
}
